import { Model, model } from '@engine/core';

@model()
export class ParameterSingleSelectPopupModel extends Model {
    public static xtype = 'report.model.main.parameter_container.controller.singleselectpopup';

    private options: {
        name4EmptyGetter: () => string;
    };

    public state() {
        return {
            name4Empty: this.options.name4EmptyGetter() || BI.i18nText('BI-Basic_No_Select'),
        };
    }

    public computed = {
    }

    public actions = {
        updateName4Empty: () => {
            this.model.name4Empty = this.options.name4EmptyGetter() || BI.i18nText('BI-Basic_No_Select');
        },
    }
}
